.team {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding-bottom: 10rem;

    @include fullHD {
        padding-bottom: 14rem;
    }

    &-txt {
        flex: 1;
        padding-top: 6rem;

        // TYTUL
        h1 {
            color: $txt;
            font-size: 5rem;
            font-weight: 600;
            font-style: normal;
            letter-spacing: normal;
            line-height: 120%;
            text-align: left;
            margin-top: 8rem;
        }

        // STANOWISKO
        .team-position {
            color: $txt;
            font-size: 2rem;
            font-weight: 400;
            font-style: normal;
            letter-spacing: normal;
            line-height: 120%;
            text-align: left;
            margin-top: 2rem;
        }

        // LINKI
        .team-link {
            @include rel-block;
            margin-top: 4rem;

            li {
                margin-bottom: 2rem;

                &:last-child {
                    margin-bottom: 0;
                }

                .phone,
                .mail {
                    display: inline-flex;
                    min-height: 4rem;
                    position: relative;
                    align-items: center;

                    &:before {
                        width: 4rem;
                        height: 4rem;
                        border-radius: 4px;
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: center;
                        align-items: center;
                        padding: 0 1rem;
                        content: ''
                    }

                    &:hover {
                        color: $black;

                        span {
                            &:after {
                                transform: scaleX(1);
                                transform-origin: bottom left;
                            }
                        }
                    }

                    span {
                        color: $txt;
                        font-family: $font;
                        font-size: 1.6rem;
                        font-weight: 400;
                        font-style: normal;
                        letter-spacing: normal;
                        line-height: 120%;
                        text-align: left;
                        @include color;
                        display: inline-block;
                        padding-left: 2rem;

                        &:after {
                            display: block;
                            content: '';
                            position: relative;
                            width: 100%;
                            height: .1rem;
                            margin-top: 0rem;
                            transform: scaleX(0);
                            background-color: $purple;
                            transform-origin: bottom right;
                            transition: transform 0.25s ease-out;
                        }
                    }
                }

                .phone {
                    &:before {
                        background: #e1e1e1 url(../icons/icon-tel-contact.svg) center no-repeat;
                    }
                }

                .mail {
                    &:before {
                        background: #e1e1e1 url(../icons/icon-mail.svg) center no-repeat;
                    }
                }

                .linkedin {
                    margin-top: 2rem;
                    display: inline-block;
                    @include link;

                    &:hover {
                        transform: translate(0px, -.3rem);
                    }
                }
            }
        }


        // INFO
        .team-info {
            @include rel-block;
            margin-top: 4rem;

            li {
                @include rel-block;
                padding-right: 3rem;
                margin-top: 6rem;

                @include mobile {
                    padding-right: 0;
                }

                h2 {
                    color: $txt;
                    font-size: 2.4rem;
                    font-weight: 600;
                    font-style: normal;
                    letter-spacing: normal;
                    line-height: 120%;
                    margin-bottom: 2rem;
                }

                p {
                    line-height: 150%;
                }

                h2,
                p {
                    max-width: 54.5rem;
                }
            }
        }
    }

    &-img {
        flex: 0 0 48.795180722891565%;

        img {
            -webkit-border-bottom-left-radius: 4rem;
            -moz-border-radius-bottomleft: 4rem;
            border-bottom-left-radius: 4rem;
            position: sticky;
            top: 7rem;
            left: 0;
            z-index: 2;
        }
    }
}


