.contact {
    @include rel-block;
    padding: 8rem 0;
    background: $bgboxes;

    @include fullHD {
        padding: 12rem 0;
    }


    // ROW
    &-row {
        display: flex;
        flex-wrap: wrap;
        width: 100%;

        // TXT
        &-txt {
            flex: 1;

            h2 {
                color: #txt;
                font-size: 3.6rem;
                font-weight: 600;
                font-style: normal;
                letter-spacing: normal;
                line-height: 120%;
                margin-bottom: 2rem;
            }

            p {
                font-size: 2rem;
                font-weight: 400;
                font-style: normal;
                letter-spacing: normal;
                line-height: 140%;
            }

            h2,
            p {
                max-width: 54.5rem;
                padding-right: 5rem;
            }
        }

        // FORM
        &-form {
            flex: 0 0 48.795180722891565%;
            position: relative;

            input[type=text],
            input[type=email],
            input[type=tel] {
                background: $white;
                border: 0;
                outline: 0;
                box-shadow: none;
                width: 100%;
                border-radius: .6rem;
                padding: 2rem;
                color: $txt;
                font-size: 1.6rem;
                font-weight: 400;
                font-style: normal;
                letter-spacing: normal;
                line-height: 100%;

                &:focus {
                    background: $white;
                }
            }

            .wpcf7-form-control-wrap {
                padding-top: 2rem;
                display: block;

                &:first-child {
                    padding-top: 0;
                }
            }

            .wpcf7-not-valid-tip {
                font-size: 1.4rem;
                padding-top: 1rem;
                padding-left: 2rem;
            }

            textarea {
                background: $white;
                border: 0;
                box-shadow: none;
                height: 15rem;
                width: 100%;
                margin-bottom: 3rem;
                resize: none;
                border-radius: .6rem;
                padding: 2rem;
                color: $txt;
                font-size: 1.6rem;
                font-weight: 400;
                font-style: normal;
                letter-spacing: normal;
                line-height: 120%;
            }

            input:-webkit-autofill,
            input:-webkit-autofill:hover,
            input:-webkit-autofill:focus,
            input:-webkit-autofill:active {
                -webkit-box-shadow: 0 0 0 30px $white inset !important;
                color: $txt;
            }

            .btn-link {
                font-size: 2.2rem;
                margin-top: 2rem;

                &:before {
                    transform: scaleX(1);
                    transform-origin: bottom left;
                }
            }


            ::placeholder {
                /* Chrome, Firefox, Opera, Safari 10.1+ */
                color: #cccccc;
                opacity: 1;
                /* Firefox */
            }

            :-ms-input-placeholder {
                /* Internet Explorer 10-11 */
                color: #cccccc;
            }

            ::-ms-input-placeholder {
                /* Microsoft Edge */
                color: #cccccc;
            }

        }

        .form-row {
            @include rel-block;
            margin-bottom: 3rem;
        }

        .form-info {
            font-size: 1.2rem;
            font-weight: 400;
            font-style: normal;
            letter-spacing: normal;
            line-height: 150%;
            color: $txt;

            a {
                color: $txt;
                @include color;
                display: inline-block;
                position: relative;

                &:after {
                    display: block;
                    content: '';
                    position: relative;
                    width: 100%;
                    height: .1rem;
                    margin-top: 0rem;
                    transform: scaleX(0);
                    background-color: $purple;
                    transform-origin: bottom right;
                    transition: transform 0.25s ease-out;
                }

                &:hover {
                    color: $black;

                    &:after {
                        transform: scaleX(1);
                        transform-origin: bottom left;
                    }
                }
            }
        }

        .button {
            button {
                background: $purple;
                height: 6rem;
                display: inline-flex;
                align-items: center;
                padding: 0 3.6rem;
                color: $white;
                font-size: 1.6rem;
                font-weight: 500;
                border-radius: .4rem;
                border: 0;
                box-shadow: none;
                @include bg;
                cursor: pointer;

                &:hover {
                    background: $txt;
                }
            }
        }
    }
}