.subsidies {
    @include rel-block;

    h2 {
        color: $txt;
        font-size: 3.6rem;
        font-weight: 600;
        font-style: normal;
        letter-spacing: normal;
        line-height: 125%;
        max-width: 55rem;
        width: 100%;

        position: sticky;
        top: 10rem;
    }

    // KOMENTARZ
    &-comments {
        @include rel-block;
        padding: 8rem;

        img {
            @include fotoBg;
            background: #3F7C9B;
            z-index: 0;
        }

        h2,
        p,
        span {
            color: $white;
            max-width: 54rem;
            width: 100%;
        }

        h2 {
            position: relative;
            top: 0;
        }

        ul {
            @include rel-block;

            li {
                margin-bottom: 2rem;
                padding-left: 2.8rem;
                position: relative;
                max-width: 100%;
                color: $white;

                &:last-child {
                    margin-bottom: 0;
                }

                &:before {
                    @include abs-block;
                    width: 2.1rem;
                    height: 2.1rem;
                    background: url('../svg/icon-checkbox-white.svg') top left no-repeat;
                    top: 0;
                    left: 0;
                    content: ''
                }
            }
        }
    }

    // TERMINY
    &-time {
        @include rel-block;
        margin-top: 8rem;
        padding-bottom: 6rem;

        &-loop {
            @include rel-block;

            li {
                margin-bottom: 2rem;
                background: $bgboxes;
                padding: 4rem;

                .date {
                    color: $txt;
                    font-size: 2rem;
                    font-weight: 600;
                    font-style: normal;
                    letter-spacing: normal;
                    line-height: 120%;
                    margin-bottom: 2rem;

                    span {
                        color: #972b8a;
                        font-size: 1.2rem;
                        font-weight: 600;
                        font-style: normal;
                        letter-spacing: 1.92px;
                        text-transform: uppercase;
                        @include rel-block;
                        width: 100%;
                        margin-bottom: .6rem;
                    }
                }

                .col {
                    flex: 1;

                    &:last-child {
                        flex: 0 0 45%;
                    }
                }
            }
        }
    }

    // TXT
    &-txt {
        @include rel-block;

        .container {
            border-top: .1rem solid #585858;
            padding: 8rem 0;
        }

        ul {
            @include rel-block;
            padding-top: 1rem;

            li {
                margin-bottom: 2rem;
                padding-left: 2.8rem;
                position: relative;
                max-width: 100%;

                &:before {
                    @include abs-block;
                    width: 2.1rem;
                    height: 2.1rem;
                    background: url('../svg/icon-checkbox-white.svg') top left no-repeat;
                    top: 0;
                    left: 0;
                    content: ''
                }
            }
        }
    }


    // FINANSE
    &-financing {
        @include rel-block;

        .container {
            border-top: .1rem solid #585858;
            padding: 8rem 0;
        }

        // PROCENTY 
        &-percent {
            font-size: 2rem;
            font-weight: 600;
            font-style: normal;
            letter-spacing: normal;
            line-height: 28px;
            color: $txt;

            div {
                @include rel-block;
                width: 100%;
                height: 2rem;
                border-radius: 1rem;
                background-color: #e1e1e1;
                margin-top: 3.2rem;
                margin-bottom: 6.8rem;

                span {
                    @include abs-block;
                    height: 100%;
                    top: 0;
                    left: 0;
                    background: $purple;
                    border-radius: 1rem;
                    @include link;
                }

            }

            &-info {
                @include rel-block;

                h4 {
                    margin-top: 6rem;
                    margin-bottom: 2rem;
                    color: $txt;
                    font-size: 2rem;
                    font-weight: bold;
                    font-style: normal;
                    letter-spacing: normal;
                    line-height: 140%;
                }

                ul {
                    @include rel-block;
                    padding-top: 0rem;
                    margin-bottom: 3rem;

                    li {
                        margin-bottom: 2rem;
                        padding-left: 2.8rem;
                        position: relative;
                        max-width: 100%;

                        &:before {
                            @include abs-block;
                            width: 2.1rem;
                            height: 2.1rem;
                            background: url('../svg/icon-checkbox-white.svg') top left no-repeat;
                            top: 0;
                            left: 0;
                            content: ''
                        }
                    }
                }
            }
        }
    }

    // WYDATKI
    &-expenses {
        @include rel-block;

        .container {
            border-top: .1rem solid #585858;
            padding: 8rem 0;
        }

        ul {
            display: flex;
            width: 100%;
            flex-wrap: wrap;
            padding-top: 7rem;

            li {
                width: 33.333%;

                .subsidies-expenses-title {
                    height: 6rem;
                    width: 100%;
                    background-color: #e1e1e1;
                    display: flex;
                    align-items: center;
                    padding-left: 4rem;
                    margin-bottom: 6rem;
                    position: relative;
                    font-size: 2rem;
                    font-weight: 600;
                    font-style: normal;
                    letter-spacing: normal;
                    line-height: 120%;

                    &:after {
                        background: url(../svg/subsidies-expenses-arrow.svg) center right no-repeat;
                        width: 3.7rem;
                        top: 50%;
                        right: 0;
                        height: 7.5rem;
                        @include abs-block;
                        content: '';
                        transform: translate(0%, -50%);
                    }
                }

                &:last-child {
                    .subsidies-expenses-title {
                        &:after {
                            display: none
                        }
                    }
                }

                ul {
                    @include rel-block;
                    padding-left: 4rem;
                    padding-top: 0;

                    li {
                        @include rel-block;
                        width: 100%;
                        margin-bottom: 2rem;
                        padding-left: 2.8rem;
                        max-width: 100%;

                        &:before {
                            @include abs-block;
                            width: 2.1rem;
                            height: 2.1rem;
                            background: url('../svg/icon-checkbox-white.svg') top left no-repeat;
                            top: 0;
                            left: 0;
                            content: ''
                        }
                    }
                }
            }
        }

    }

}