.market-insights {
    @include rel-block;
    width: 100%;
    max-width: 100rem;
    margin: 8rem auto 0;

    .container {
        width: 100% !important;
        padding-bottom: 10rem;
    }

    img {
        max-width: 100%;
        height: auto;
    }

    // BOLD
    &-bold {
        @include rel-block;
        margin-bottom: 6rem;

        p {
            font-size: 2.4rem;
            font-weight: 600;
            font-style: normal;
            letter-spacing: normal;
            line-height: 125%;
        }
    }

    // TXT 
    &-txt {
        @include rel-block;

        p {
            &:last-child {
                margin-bottom: 2rem !important;
            }
        }

        h2,
        h3,
        h4 {
            color: $txt;
            font-weight: 600;
            line-height: 120%;
            margin-bottom: 2rem;
        }

        h2 {
            font-size: 2.6rem;
        }

        h3 {
            font-size: 2.4rem;
        }

        h4 {
            font-size: 2rem
        }

        ul {
            @include rel-block;

            li {
                padding-left: 2rem;
                @include rel-block;
                margin-bottom: 2rem;

                &:last-child {
                    margin-bottom: 0;
                }

                &:before {
                    @include abs-block;
                    width: .6rem;
                    height: .6rem;
                    background-color: #972b8a;
                    border-radius: 50%;
                    top: .5rem;
                    left: 0;
                    content:'';
                }
            }
        }

    }

    // FOTO
    &-foto {
        @include rel-block;
        margin: 4rem 0 6rem 0rem;

        img {
            max-width: 100%;
            height: auto;
        }
    }
}

// BNT BACK
.market-insights-btn {
    @include rel-block;
    margin-bottom: 8rem;
}